@charset "utf-8";

/*fonts*/
@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 300;
    src: url('../fonts/NotoSansKR-DemiLight-Hestia.eot');
    src: url('../fonts/NotoSansKR-DemiLight-Hestia.eot?') format('eot'), url('../fonts/NotoSansKR-DemiLight-Hestia.woff') format('woff');
}

@font-face {
    font-family: 'Noto Sans KR';
    font-style: normal;
    font-weight: 600;
    src: url('../fonts/NotoSansKR-Bold-Hestia.eot');
    src: url('../fonts/NotoSansKR-Bold-Hestia.eot?') format('eot'), url('../fonts/NotoSansKR-Bold-Hestia.woff') format('woff');
}

/* Common */
*,
*:before,
*:after {
    -webkit-box-sizing: border-box;
    box-sizing: border-box
}

html,
body,
div,
span,
applet,
object,
iform-area,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    font-family: 'Noto Sans KR', '맑은 고딕', sans-serif;
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    vertical-align: baseline;
    background: transparent;
    font-size: 14px;
    letter-spacing: 0;
    line-height: 1.5;
    color: #333;
}

body {
    line-height: 1;
}

html,
body {
    min-height: 100%;
}

ul {
    list-style: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    table-layout: fixed;
}

td,
th {
    border-collapse: collapse;
    border-spacing: 0;
    word-wrap: break-word;
    word-break: break-all;
}

caption {
    overflow: hidden;
    width: 1px;
    height: 1px;
    margin: -1px 0px;
    font-size: 1px;
    line-height: 0;
}

a {
    display: block;
    text-decoration: none;
}

img,
fieldset,
button,
input,
select,
textarea {
    border: 0 none;
    -moz-appearance: none;
    appearance: none;
    -webkit-appearance: none;
}

img {
    max-width: 100%;
}

input {
    border: 0;
    background: #fff
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
    color: #999 !important;
}

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
    color: #999 !important;
}

img,
input,
select,
button,
label {
    vertical-align: top;
    font-size: 16px;
}

input[disabled="disabled"] {
    background: #f8f8f8 !important
}

button:disabled,
button[disabled] {
    background: #ddd !important;
    color: #aaa !important;
    cursor: default !important
}

h1 {
    font-size: 27px;
}

.blind {
    display: inline-block;
    text-indent: -9999px;
}

.margin0 {
    margin-top: 0px !important;
}

.margin10 {
    margin-top: 10px !important;
}

/*style*/
#wrap {
    -webkit-text-size-adjust: 100%;
    min-width: 320px;
    max-width: 648px;
    margin: 0 auto;
}

.container {
    background: #e7e9ed;
    overflow: hidden;
}

.event-wrap {
    max-width: 565px;
    margin: 30px auto 40px auto;
}

.event-wrap .form-area {
    max-width: 565px;
    margin: 0 auto 40px;
    overflow: hidden;
    background: #fff;
}

.event-wrap .title-area {
    text-align: center;
    position: relative;
    overflow: hidden;
    font-size: 0;
    margin-bottom: -28px;
}

.event-wrap .title-area h1 {
    display: inline-block;
    height: 56px;
    line-height: 56px;
    background: #626075;
    color: #fff;
    vertical-align: middle;
    padding: 0 7%;
}

.event-wrap .title-area h1.type2 {
    padding: 0;
}

.event-wrap .title-area:before {
    content: '';
    display: inline-block;
    width: 15px;
    height: 56px;
    background: url('../img/event_2019/0425/title-bg.png') no-repeat;
    vertical-align: middle;
}

.event-wrap .title-area:after {
    content: '';
    display: inline-block;
    width: 15px;
    height: 56px;
    background: url('../img/event_2019/0425/title-bg2.png') no-repeat;
    vertical-align: middle;
}

.event-wrap .input-warp {
    background: url('../img/event_2019/0425/bg_line.png') no-repeat left bottom;
    background-size: 100% auto;
}

.event-wrap .input-warp p.text {
    font-size: 26px;
    color: #000;
    line-height: 150%;
    text-align: center;
    padding: 55px 0 35px 0;
}

.event-wrap .input-warp h2 {
    font-size: 18px;
    color: #4a4a4a;
    max-width: 420px;
    margin: 0 auto;
    margin-top: 20px;
    font-weight: 500;
}

.event-wrap .input-warp h2:before {
    content: '';
    display: inline-block;
    width: 4px;
    height: 10px;
    background: #4a4a4a;
    margin-right: 6px;
    margin-bottom: 2px;
}

.event-wrap .input-warp p {
    max-width: 430px;
    margin: 0 auto;
    padding: 0 0 35px;
}

.event-wrap .input-warp div.inner {
    max-width: 430px;
    margin: 0 auto;
    padding: 0 0 35px;
}

.event-wrap .input-warp div.inner .notice-desc {
    position: relative;
    padding-left: 12px;
    color: #f70000;
    font-size: 16px;
    padding-bottom: 0;
    margin: 15px 0 0 0;
}

.event-wrap .input-warp div.inner .notice-desc:before {
    content: '*';
    position: absolute;
    left: 3px;
    top: 3px;
}

.event-wrap .input-warp div.inner .notice-desc+.notice-desc {
    margin: 0;
}

.event-wrap .input-warp input[type="number"],
.event-wrap .input-warp input[type="text"],
.event-wrap .input-warp input[type="tel"] {
    width: 100%;
    height: 50px;
    line-height: 50px;
    margin-top: 15px;
    text-indent: 28px;
    background: #f8f8f8;
    color: #595959;
    font-family: 'Noto Sans KR', '맑은 고딕', sans-serif;
}

.event-wrap .input-warp input[type="tel"]::-webkit-inner-spin-button,
.event-wrap .input-warp input[type="tel"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0;
}

.event-wrap .chk-warp {
    max-width: 430px;
    margin: 0 auto;
    padding: 25px 0 0;
}

.event-wrap .chk-warp ul {
    overflow: hidden;
}

.event-wrap .chk-warp ul li {
    padding-bottom: 10px;
    position: relative;
    padding-right: 80px;
}

.event-wrap .chk-warp ul li .link-btn {
    position: absolute;
    right: 0;
    top: 3px;
    font-size: 14px;
    color: #777;
    text-decoration: underline;
    background: transparent;
    cursor: pointer;
    padding: 0;
    letter-spacing: -0.05em;
}

.btn-area {
    padding: 25px 20px 30px;
}

.btn-area button {
    display: block;
    max-width: 430px;
    width: 100%;
    margin: 0 auto;
    background: #f5202f;
    color: #fff;
    font-size: 21px;
    line-height: 70px;
    height: 70px;
    text-align: center;
    border: 0;
    font-weight: 600;
}

/*checkbox style*/
.event-wrap .chk-warp [type="checkbox"]:not(:checked),
.event-wrap .chk-warp [type="checkbox"]:checked {
    position: absolute;
    left: -9999px;
}

.event-wrap .chk-warp [type="checkbox"]:not(:checked)+label,
.event-wrap .chk-warp [type="checkbox"]:checked+label {
    position: relative;
    padding-left: 25px;
    cursor: pointer;
    color: #4a4a4a;
    letter-spacing: -0.05em;
}

.event-wrap .chk-warp [type="checkbox"]:not(:checked)+label:before,
.event-wrap .chk-warp [type="checkbox"]:checked+label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 3px;
    width: 21px;
    height: 21px;
    background: url('../img/event_2019/0425/chk-icon.png') no-repeat -22px 0;
}

.event-wrap .chk-warp [type="checkbox"]:not(:checked)+label:after,
.event-wrap .chk-warp [type="checkbox"]:checked+label:after {
    content: '';
    position: absolute;
    left: 0;
    top: 3px;
    width: 21px;
    height: 21px;
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    background: url('../img/event_2019/0425/chk-icon.png') no-repeat;
}

.event-wrap .chk-warp [type="checkbox"]:not(:checked)+label:after {
    opacity: 0;
}

.event-wrap .chk-warp [type="checkbox"]:checked+label:after {
    opacity: 1;
}

.event-wrap .chk-warp [type="checkbox"]+label span {
    font-size: 16px;
    color: #aaa;
}

/* field-set */
.field-set {
    position: relative;
    margin-top: 0;
}

.field-set:first-child {
    margin-top: 15px;
}

.field-set .form-set {
    color: #000;
}

.field-set .form-set:focus {
    outline: 1px solid #4a4a4a;
}

.field-set.check-type {
    padding: 0;
}

.field-set.btn-type .form-set {
    width: 70% !important;
    margin-top: 0 !important;
}

.field-set.btn-type .count-down-area {
    position: absolute;
    top: 50%;
    right: 35%;
    transform: translateY(-50%);
}

.field-set.btn-type .count-down-area,
.field-set.btn-type .count-down-area span {
    font-size: 15px;
    color: #f5202f;
}

.field-set.btn-type button {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 50px;
    width: 30%;
    background: #434159;
    color: #fff;
    cursor: pointer;
    word-break: keep-all;
    font-size: 15px;
}

.check-type .check-box+.form-set {
    margin-top: 12px;
}

.check-type {
    margin-top: 15px;
}

.check-type+.check-type {
    margin-top: 26px;
}

.field-set+.field-set {
    margin-top: 15px;
}

/* check-box */
.check-box {
    position: relative;
    padding-left: 40px;
    text-align: left;
    display: inline-block;
    font-size: 16px;
    height: 30px;
    line-height: 30px;
    cursor: pointer;
}

.check-box input[type="checkbox"] {
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 1px;
    z-index: 0;
}

.check-box input[type="checkbox"]+.fake {
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 30px;
    background: url("../img/event_2019/0425/unchecked-input.png") left top no-repeat;
    background-size: contain;
    z-index: 1;
}

.check-box input[type="checkbox"]:checked+.fake {
    background: url("../img/event_2019/0425/checked-input.png") no-repeat;
    background-size: contain
}

.info {
    background: #434159;
    padding: 45px 35px;
}

.info h2,
.info p,
.info ol li,
.info ul li {
    color: #fff;
}

.info h2 {
    font-size: 21px;
}

.info p,
.info ul li {
    position: relative;
    margin-top: 10px;
    font-size: 16px;
    line-height: 24px;
    padding-left: 10px;
}

.info p:before,
.info ul li:before {
    content: '';
    position: absolute;
    width: 4px;
    height: 12px;
    top: 6px;
    left: 0;
    background: #fff;
}

.info p>a {
    display: inline-block;
    font-size: 18px;
}

.info ol {
    padding-left: 15px;
    margin-top: 20px;
}

.info ol li {
    color: #bbb;
    line-height: 29px;
    font-size: 16px;
}

/*modal*/
.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 0;
    padding: 15px;
    display: none;
    background: rgba(0, 0, 0, 0.75);
    cursor: pointer;
    z-index: 99;
}

.modal.open {
    display: block;
}

.modal-container {
    position: absolute;
    display: block;
    width: 628px;
    max-height: 710px;
    padding: 50px 22px 30px;
    overflow: auto;
    background: #fff;
    cursor: default;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.modal-container .contents {
    position: relative;
}

.modal-container .close-icon {
    position: absolute;
    top: -28px;
    right: 0;
    width: 22px;
    height: 22px;
    background: url('../img/event_2019/0425/close_icon.png') no-repeat;
    padding: 0;
    z-index: 100;
    cursor: pointer;
}

.modal-container .contents .top h2 {
    font-size: 26px;
}

.modal-container .contents .mid {
    margin: 30px 0 25px;
    background: #f8f8f8;
    padding: 26px 30px;
}

.modal-container .contents .mid p,
.modal-container .contents .mid ol li,
.modal-container .contents .mid ul li {
    color: #000;
}

.modal-container .contents .mid ol {
    padding-left: 15px;
}

.modal-container .contents .bottom {
    position: relative;
    display: block;
    text-align: center;
}

.modal-container .contents .bottom button {
    width: 430px;
    height: 70px;
    line-height: 70px;
    background: #434159;
    color: #fff;
    font-size: 21px;
    font-weight: bold;
    cursor: pointer;
}

/*bullet_style*/
.info .bullet_style li p {
    color: #bfb9ff;
    line-height: 20px;
    padding-left: 0;
    margin-top: 0;
}

.info .bullet_style li:before {
    width: 3px;
    height: 3px;
    border-radius: 3px;
    top: 12px;
}

.info .bullet_style p:before {
    display: none;
}

body.modal-open {
    overflow: hidden !important;
}

@media only screen and (max-width: 648px) {
    h2 {
        font-size: 17px !important;
    }

    .container .event-wrap {
        margin: 20px auto;
        padding: 0 4%;
    }

    .container .event-wrap .title-area {
        margin-bottom: -18px;
        white-space: nowrap;
    }

    .container .event-wrap .title-area h1 {
        font-size: 20px;
        height: 40px;
        line-height: 40px;
    }

    .container .event-wrap .title-area h1 img {
        height: 40px;
    }

    .container .event-wrap .title-area:before,
    .container .event-wrap .title-area:after {
        height: 40px;
        background-size: 77%;
    }

    .container .event-wrap .title-area:before {
        background-position-x: 4px;
    }

    .container .event-wrap .input-warp p.text {
        font-size: 18px;
        padding: 30px 0 20px 0;
    }

    .container .event-wrap .input-warp h2 {
        margin: 20px 20px 0 20px;
    }

    .container .event-wrap .input-warp p,
    .container .event-wrap .input-warp div.inner {
        padding: 10px 20px 30px 20px;
        max-width: inherit;
    }

    .container .event-wrap .input-warp input[type="text"],
    .event-wrap .input-warp input[type="tel"] {
        height: 40px;
        line-height: 40px;
        font-size: 13px;
        text-indent: 20px;
    }

    .container .event-wrap .form-area .chk-warp {
        padding: 15px 20px 0 20px;
        max-width: inherit;
    }

    .container .event-wrap .form-area .chk-warp ul li input[type="checkbox"]+label,
    .container .event-wrap .form-area .chk-warp ul li span {
        font-size: 14px;
    }

    .event-wrap .chk-warp ul li .link-btn {
        font-size: 13px;
    }

    .event-wrap .chk-warp [type="checkbox"]:not(:checked)+label:before,
    .event-wrap .chk-warp [type="checkbox"]:checked+label:before,
    .event-wrap .chk-warp [type="checkbox"]:not(:checked)+label:after,
    .event-wrap .chk-warp [type="checkbox"]:checked+label:after {
        top: 0;
    }

    .container .event-wrap .form-area .btn-area {
        padding: 10px 20px 20px
    }

    .container .event-wrap .form-area .btn-area button {
        font-size: 18px;
        height: 50px;
        line-height: 50px;
        max-width: inherit;
    }

    .event-wrap .input-warp div.inner .notice-desc {
        font-size: 12px;
        padding: 0 0 0 12px
    }

    .container .info {
        padding: 35px 20px;
    }

    .container .info h2 {
        font-size: 25px;
    }

    .container .info p {
        margin-top: 20px;
        font-size: 18px;
    }

    .container .info p:before {
        height: 16px;
        top: 7px;
    }

    .container .info ol {
        margin-top: 10px;
    }

    .container .info ol li {
        font-size: 13px;
    }

    .modal-container {
        width: 90%;
        max-height: 90%;
        margin: 0 auto;
        padding: 40px 22px 22px;
    }

    .modal-container .close-icon {
        top: -13px;
    }

    .modal-container .contents .top h2 {
        font-size: 20px;
    }

    .modal-container .contents .mid {
        margin: 22px 0 20px;
        padding: 22px;
    }

    .modal-container .contents .bottom button {
        width: 65%;
        height: 50px;
        line-height: 50px;
        font-size: 17px;
    }

    .field-set.btn-type button {
        height: 40px;
        font-size: 12px;
    }

    .check-box+.form-set {
        margin-top: 6px;
    }

    .check-box {
        position: relative;
        padding-left: 32px;
        font-size: 14px;
        height: 24px;
        line-height: 24px;
    }

    .check-box input[type="checkbox"]+.fake {
        width: 24px;
        height: 24px;
    }

    body.modal-open {
        overflow: hidden !important;
        position: fixed !important;
    }

    .info p,
    .info ul li,
    .info ol li {
        margin-top: 10px;
        font-size: 14px;
    }

    .info ul li,
    .info ol li {
        line-height: 1.5;
    }

    .info p:before,
    .info ul li:before {
        top: 5px;
        height: 10px;
    }

    .field-set.btn-type .count-down-area,
    .field-set.btn-type .count-down-area span {
        font-size: 14px;
    }

    /*bullet_style*/
    .info .bullet_style li p {
        font-size: 14px;
    }
}

@media only screen and (max-width: 374px) {
    .container .event-wrap .form-area .chk-warp ul li input[type="checkbox"]+label {
        display: block;
    }

    .modal-container .close-icon {
        top: -23px;
        right: -5px;
    }

    .container .info p {
        font-size: 15px;
        line-height: 23px;
    }

    .container .info p:before {
        top: 4px;
    }

    .container .info ol li {
        line-height: 21px;
    }

    body.modal-open {
        overflow: hidden !important;
        position: fixed !important;
    }
}